import React from "react";
import { Link as RouteLink } from "react-router-dom";
import { Link as PageLink, animateScroll as scroll } from "react-scroll";

function scrollToTop() {
  scroll.scrollToTop();
}

function LandingNavbar(props) {
  const [navbarOpen, setNavbarOpen] = React.useState(false);
  return (
    <>
       <nav
        className={
          (props.transparent
            ? "top-0 absolute z-50 w-full"
            : "top-0 fixed z-50 w-full shadow-lg bg-white") +
          " flex flex-wrap items-center justify-between px-2 py-0 navbar-expand-lg"
        }
      >
        <div className="container px-4 mx-auto flex flex-wrap items-center justify-between">
          <div className="w-full relative flex justify-between lg:w-auto lg:static lg:block lg:justify-start">
            <div
              className={
                (props.transparent ? "text-white" : "text-gray-800") +
                " text-sm font-bold leading-relaxed inline-block mr-4 py-2 whitespace-no-wrap uppercase"
              }
            >
              <PageLink to="/">
                <img
                  alt="Authored"
                  className="h-16 cursor-pointer"
                  src={`https://authored-assets.s3.us-east-2.amazonaws.com/logo2.jpg`}
                />
              </PageLink>
            </div>
            <button
              className="cursor-pointer text-xl leading-none px-3 py-1 border border-solid border-transparent rounded bg-transparent block lg:hidden outline-none focus:outline-none"
              type="button"
              onClick={() => setNavbarOpen(!navbarOpen)}
            >
              <i
                className={
                  (props.transparent ? "text-white" : "text-gray-800") +
                  " fas fa-bars"
                }
              ></i>
            </button>
          </div>
          <div
            className={
              "lg:flex flex-grow items-center bg-white lg:bg-transparent lg:shadow-none" +
              (navbarOpen ? " block rounded shadow-lg" : " hidden")
            }
            id="example-navbar-warning"
          >
            <ul className="flex flex-col lg:flex-row list-none lg:ml-auto">
              <li className="flex items-center">
                <PageLink
                  activeClass="active"
                  to="authorSection"
                  spy={true}
                  smooth={true}
                  duration={1000}
                  className={
                    (props.transparent
                      ? "lg:text-white lg:hover:text-gray-300 text-gray-800"
                      : "text-gray-800 hover:text-gray-600") +
                    " px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold cursor-pointer"
                  }
                >
                  For Aspiring Authors
                </PageLink>
              </li>

              <li className="flex items-center">
                <PageLink
                  activeClass="active"
                  to="agentSection"
                  spy={true}
                  smooth={true}
                  duration={1000}
                  className={
                    (props.transparent
                      ? "lg:text-white lg:hover:text-gray-300 text-gray-800"
                      : "text-gray-800 hover:text-gray-600") +
                    " px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold cursor-pointer"
                  }
                >
                  For Literary Agents
                </PageLink>
              </li>

              <li className="flex items-center">
                <PageLink
                  activeClass="active"
                  to="contactSection"
                  spy={true}
                  smooth={true}
                  duration={1000}
                  type="button"
                  style={{ transition: "all .15s ease" }}
                  className={
                    (props.transparent
                      ? "bg-white text-gray-800 active:bg-gray-100"
                      : "bg-gray-900 text-white active:bg-gray-600") +
                    " text-xs font-bold uppercase px-4 py-2 rounded shadow hover:shadow-md outline-none focus:outline-none lg:mr-1 lg:mb-0 ml-3 mb-3 cursor-pointer"
                  }
                >
                  Contact Us
                </PageLink>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}

export default LandingNavbar;
