import axios from "axios";
import * as constants from '../constants';

const USER_TOKEN_NAME = "userToken";
const USER_PROFILE_NAME = "userProfile";

class AuthService {

    login(username, password) {
        return axios
            .post(constants.API_URL + "login/", {
                'email': username,
                'password': password,
            })
            .then(response => {
                if (response.data.access) {
                    localStorage.setItem(USER_TOKEN_NAME, JSON.stringify(response.data));
                }
                return response.data;
            });
    }

    logout() {
        localStorage.removeItem(USER_TOKEN_NAME);
    }

    register(first_name, last_name, role, email, password, password2) {
        return axios.post(constants.API_URL + "register/", {
            first_name,
            last_name,
            role,
            email,
            password,
            password2,
        });
    }

    getCurrentUserData() {
        let userTokens = JSON.parse(localStorage.getItem(USER_TOKEN_NAME));
        let accessPayload = userTokens;
        return {
            tokens: userTokens,
            accessPayload: accessPayload
        };
    }

}
export default new AuthService();
