import React from "react";
import Navbar from "../components/landingNavbar";
import Footer from "../components/landingFooter";
import Form from "../components/newsletterForm";
import product from "../assets/images/product.gif";
import agentFilters from "../assets/images/agentFilters.png";
import Typical from "react-typical";

function Landing() {
  const TypingAnimation =  React.memo(()=>{
    return <Typical
    loop={Infinity}
    steps={[
      'daunting for authors.',
      500,
      'time consuming for agents.',
      500
    ]}
   />
  },(props,prevProp)=> true );
  return (
    <>
    <div class="flex flex-col h-screen justify-between">
      <Navbar />
      <main className="mb-auto">
        <div className="relative pt-16 pb-32 flex content-center items-center justify-center"
            style={{
              minHeight: "75vh"
            }}>
          <div className="absolute top-0 w-full h-full bg-center bg-cover"
              style={{
                backgroundImage: `url("https://authored-assets.s3.us-east-2.amazonaws.com/background.jpg")`
              }}>
            <span id="blackOverlay" className="w-full h-full absolute opacity-50 bg-black"></span>
          </div>
          <div className="container relative mx-auto">
              <div className="items-center flex flex-wrap ml-12">
                <div className="w-full lg:w-6/12 px-4 ml-auto mr-auto text-center">
                  <div className="pr-12">
                    <h1 className="text-white font-semibold lg:text-4xl text-3xl">
                      Querying is
                      <TypingAnimation />
                    <br />
                    We are the solution.
                    </h1>
                  </div>
                </div>
              </div>
          </div>
        </div>

        <section className="pb-8 bg-white -mt-32">
          <div className="container mx-auto px-4">
          <div className="flex flex-wrap">
              <div className="w-full md:w-4/12 px-4 text-center">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-8 shadow-lg rounded-lg">
                  <div className="md:px-4 md:py-5 px-2 py-3 flex-auto">
                    <div className="text-white p-3 text-center inline-flex items-center justify-center w-12 h-12 mb-5 shadow-lg rounded-full bg-gray-500">
                      <i className="fas fa-pen-nib"></i>
                    </div>
                    <h6 className="lg:text-xl text-lg font-semibold">
                      For Aspiring Authors
                    </h6>
                    <p className="mt-2 mb-4 text-black lg:text-base text-xs">
                      By querying through the platform, authors automatically track their queries and unlock insights to help them improve the quality of their queries.
                    </p>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-4/12 px-4 text-center">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-8 shadow-lg rounded-lg">
                  <div className="md:px-4 md:py-5 px-2 py-3 flex-auto">
                    <div className="text-white p-3 text-center inline-flex items-center justify-center w-12 h-12 mb-5 shadow-lg rounded-full bg-gray-500">
                      <i className="fas fa-desktop"></i>
                    </div>
                    <h6 className="lg:text-xl text-lg font-semibold">
                      The Platform
                    </h6>
                    <p className="mt-2 mb-4 text-black lg:text-base text-xs">
                    Authored is a data driven platform that connects authors and agents.
                    </p>
                  </div>
                </div>
              </div>

              <div className="w-full md:w-4/12 px-4 text-center">
                <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-8 shadow-lg rounded-lg">
                  <div className="md:px-4 md:py-5 px-2 py-3 flex-auto">
                    <div className="text-white p-3 text-center inline-flex items-center justify-center w-12 h-12 mb-5 shadow-lg rounded-full bg-gray-500">
                      <i className="fas fa-book-open"></i>
                    </div>
                    <h6 className="lg:text-xl text-lg font-semibold">
                      For Literary Agents
                    </h6>
                    <p className="mt-2 mb-4 text-black lg:text-base text-xs">
                    Agents get access to advanced filters never seen before in the publishing industry, allowing them to spend more time on the best quality queries.
                    </p>
                  </div>
                </div>
              </div>
            </div>

            <div id="authorSection" className="mb-24"></div>

            <div className="flex flex-wrap items-center mt-12">
              <div className="w-full lg:w-5/12 px-4 mr-auto ml-auto">
                <h3 className="lg:text-3xl text-2xl mb-2 font-semibold leading-normal">
                  What's in it for <span className="text-gray-500">Authors</span>
                </h3>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-comments text-gray-500 mr-2"></i>
                  Feedback on Queries
                  <p className="text-sm">See how many agents are reading your query, synopsis, and opening chapters.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-chart-area text-gray-500 mr-2"></i>
                  Advanced Analytics
                  <p className="text-sm">Track statistics, such as interest level of agents, over time and see how your stats compare to peers.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-crosshairs text-gray-500 mr-2"></i>
                  Automated Query Tracking
                  <p className="text-sm">Automatically track all queries submitted on the platform.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-search text-gray-500 mr-2"></i>
                  Agent Search
                  <p className="text-sm">Find agents that are interested in authors similar to you with our proprietary searching feature.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-layer-group text-gray-500 mr-2"></i>
                  Community Slush Pile
                  <p className="text-sm">Pitch your book publicly through our Community Slush Pile without the 280-character limit.</p>
                </div>
              </div>

              <div className="w-full lg:w-6/12 px-4 mr-auto ml-auto">
                <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg">
                  <img
                    alt="..."
                    src={product}
                    className="w-full align-middle rounded-t-lg"
                  />
                </div>
              </div>
            </div>
          </div>
        </section>

        <div id="agentSection" className="mb-24"></div>

        <section className="relative pb-20">
          <div className="container mx-auto px-4">
            <div className="items-center flex flex-wrap">
              <div className="w-full lg:w-6/12 px-4 mr-auto ml-auto">
                <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg">
                  <img
                    alt="..."
                    src={agentFilters}
                    className="w-full align-middle rounded-t-lg"
                  />
                </div>
              </div>
              <div className="w-full lg:w-5/12 ml-auto mr-auto px-4">
              <h3 className="lg:text-3xl text-2xl mb-2 font-semibold leading-normal">
                  What's in it for <span className="text-gray-500">Agents</span>
                </h3>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-filter text-gray-500 mr-2"></i>
                  Spend more time on the best queries
                  <p className="text-sm">Filter your query inbox by quality of query!</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-filter text-gray-500 mr-2"></i>
                  Other Advanced Filtering Tools
                  <p className="text-sm">Create and save custom filters that match the genre(s) you are interested in representing. Access these filters with a click of a button.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-crosshairs text-gray-500 mr-2"></i>
                  Author Scouting
                  <p className="text-sm">Search for top talent by using our filters on every author on our platform. Access the author's query, synopsis, and chapters by going to his/her profile.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-random text-gray-500 mr-2"></i>
                  Easy Transition
                  <p className="text-sm">Easily upload your query inbox to our platform and post a button on your website to send non-Authored queries to your inbox.</p>
                </div>
                <div className="text-lg font-light leading-relaxed mt-4 mb-4 text-black">
                  <i className="fas fa-chart-bar text-gray-500 mr-2"></i>
                  Smart Insights
                  <p className="text-sm">Receive analytics that show key statistics, how they have trended, and how they compare to similar agents.</p>
                </div>
              </div>
            </div>
          </div>
        </section>

        <div id="contactSection" className="mb-24"></div>

        <section className="relative block bg-gray-900">
          <div className="container mx-auto px-4 pt-12 lg:pb-64">
            <div className="flex flex-wrap text-center justify-center">
              <div className="lg:w-6/12 w-full px-4">
                <h2 className="md:text-3xl text-xl font-semibold text-white">
                  Don't get held up at the finish line.
                </h2>
                <h2 className="md:text-3xl text-xl font-semibold text-white">
                  Get Authored.
                </h2>
                <p className="md:text-lg text-sm leading-relaxed mt-4 mb-4 text-gray-500">
                  Send us an email if you are interested in the product or want to join the team!
                </p>
                
              </div>
            </div>
          </div>
        </section>

        <section className="relative block py-12 pt-0 bg-gray-900">
          <div className="container mx-auto px-4">
            <div className="flex flex-wrap justify-center lg:-mt-64">
              <div className="lg:w-6/12 md:w-8/12 w-full px-4">
                <div className="relative flex flex-col min-w-0 break-words w-full shadow-lg rounded-lg bg-gray-300">
                  <div className="flex-auto p-5 lg:p-10">
                    <Form />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

      </main>
      <Footer absolute="true"/>
    </div>
    </>
  );
}

export default Landing;
