import React, { Component } from "react";
import { Link } from "react-router-dom";
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import { isEmail } from "validator";

import Footer from "../components/landingFooter";
import AuthService from "../services/auth.service";
import Navbar from "../components/landingNavbar";

const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

const email = value => {
  if (!isEmail(value)) {
    return (
      <div className="alert alert-danger" role="alert">
        This is not a valid email.
      </div>
    );
  }
};

const vusername = value => {
  if (value.length < 3 || value.length > 200) {
    return (
      <div className="alert alert-danger" role="alert">
        The email address must be between 3 and 200 characters.
      </div>
    );
  }
};

const vpassword = value => {
  if (value.length < 6 || value.length > 100) {
    return (
      <div className="alert alert-danger" role="alert">
        The password must be between 6 and 40 characters.
      </div>
    );
  }
};

const vrole = value => {
  if (value.length < 6 || value.length > 100) {
    return (
      <div className="alert alert-danger" role="alert">
        Your role must me Author or Agent.
      </div>
    );
  }
};

export default class Register extends Component {
  constructor(props) {
    super(props);
    this.handleRegister = this.handleRegister.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangeFirstName = this.onChangeFirstName.bind(this);
    this.onChangeLastName = this.onChangeLastName.bind(this);
    this.onChangeRole = this.onChangeRole.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.onChangePassword2 = this.onChangePassword2.bind(this);
    this.state = {
      first_name: "",
      last_name: "",
      role: "",
      email: "",
      password: "",
      password2: "",
      successful: false,
      message: ""
    };
  }

  onChangeUsername(e) {
    this.setState({
      email: e.target.value
    });
  }

  onChangeFirstName(e) {
    this.setState({
      first_name: e.target.value
    });
  }

  onChangeLastName(e) {
    this.setState({
      last_name: e.target.value
    });
  }

  onChangeRole(e) {
    this.setState({
      role: e.target.value
    });
  }

  onChangePassword(e) {
    this.setState({
      password: e.target.value
    });
  }

  onChangePassword2(e) {
    this.setState({
      password2: e.target.value
    });
  }
  
  handleRegister(e) {
    e.preventDefault();
    this.setState({
      message: "",
      successful: false
    });
    this.form.validateAll();
    if (this.checkBtn.context._errors.length === 0) {
      AuthService.register(
        this.state.first_name,
        this.state.last_name,
        this.state.role,
        this.state.email,
        this.state.password,
        this.state.password2,
      ).then(
        response => {
          this.setState({
            message: response.data.message,
            successful: true
          });
        },
        error => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.setState({
            successful: false,
            message: resMessage
          });
        }
      );
    }
  }

  render() {
    return (
      <>
      <Navbar />
      <main>
      <section className="absolute w-full h-full">
        <div className="container mx-auto px-4 h-full">
          <div className="flex content-center items-center justify-center h-full">
            <div className="w-full lg:w-4/12 px-4">
              <Link
              to="/"
              >
              <img
              alt="Authored"
              className="cursor-pointer mx-auto h-16 w-auto"
              src={`https://authored-assets.s3.us-east-2.amazonaws.com/logo2.jpg`}
              />
              </Link>
              <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-300 border-0">
                <div className="rounded-t mb-0 px-6 py-6">
                  <div className="text-center mb-3">
                    <h6 className="text-gray-700 text-sm font-bold">
                      Sign up with
                    </h6>
                  </div>
                  <div className="btn-wrapper text-center">
                    <button
                      className="bg-white active:bg-gray-100 text-gray-800 px-4 py-2 rounded outline-none focus:outline-none mr-1 mb-1 uppercase shadow hover:shadow-md inline-flex items-center font-bold text-xs"
                      type="button"
                      style={{ transition: "all .15s ease" }}
                    >
                      <i className="fab fa-facebook mr-2"></i>
                      Facebook
                    </button>
                    <button
                      className="bg-white active:bg-gray-100 text-gray-800 px-4 py-2 rounded outline-none focus:outline-none mr-2 mb-1 uppercase shadow hover:shadow-md inline-flex items-center font-bold text-xs"
                      type="button"
                      style={{ transition: "all .15s ease" }}
                    >
                      <i className="fab fa-google mr-2"></i>
                      Google
                    </button>
                  </div>
                  <hr className="mt-6 border-b-1 border-gray-400" />
                </div>
                <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
                  <div className="text-gray-700 text-center mb-3 font-bold">
                    <small>Or sign in with credentials</small>
                  </div>
                  <Form
                    onSubmit={this.handleRegister}
                    ref={c => {
                      this.form = c;
                    }}
                  >
                    {!this.state.successful && (
                      <div>
                        <div className="form-group relative w-full mb-3">
                          <label 
                            className="block uppercase text-gray-700 text-xs font-bold mb-2"
                            htmlFor="email"
                            >
                              Email Address
                          </label>
                          <Input
                            type="email"
                            className="form-control px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full"
                            name="email"
                            placeholder="Email"
                            style={{ transition: "all .15s ease" }}
                            value={this.state.email}
                            onChange={this.onChangeUsername}
                            validations={[required, vusername]}
                          />
                        </div>
                        <div className="form-group relative w-full mb-3">
                          <label 
                            className="block uppercase text-gray-700 text-xs font-bold mb-2"
                            htmlFor="first_name"
                            >
                              First Name
                          </label>
                          <Input
                            type="text"
                            className="form-control px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full"
                            name="first_name"
                            placeholder="First Name"
                            style={{ transition: "all .15s ease" }}
                            value={this.state.first_name}
                            onChange={this.onChangeFirstName}
                            validations={[required, vusername]}
                          />
                        </div>
                        <div className="form-group relative w-full mb-3">
                          <label 
                            className="block uppercase text-gray-700 text-xs font-bold mb-2"
                            htmlFor="first_name"
                            >
                              Last Name
                          </label>
                          <Input
                            type="text"
                            className="form-control px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full"
                            name="last_name"
                            placeholder="Last Name"
                            style={{ transition: "all .15s ease" }}
                            value={this.state.last_name}
                            onChange={this.onChangeLastName}
                            validations={[required, vusername]}
                          />
                        </div>
                        <div className="form-group relative w-full mb-3">
                          <label 
                            className="block uppercase text-gray-700 text-xs font-bold mb-2"
                            htmlFor="role"
                            >
                              I am an...
                          </label>
                          <Input
                            type="text"
                            className="form-control px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full"
                            name="role"
                            placeholder="Author or Agent"
                            style={{ transition: "all .15s ease" }}
                            value={this.state.role}
                            onChange={this.onChangeRole}
                            validations={[required, vrole]}
                          />
                        </div>
                        <div className="form-group relative w-full mb-3">
                          <label 
                            htmlFor="password"
                            className="block uppercase text-gray-700 text-xs font-bold mb-2"
                          >
                            Password
                          </label>
                          <Input
                            type="password"
                            className="form-control px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full"
                            name="password"
                            placeholder="Password"
                            style={{ transition: "all .15s ease" }}
                            value={this.state.password}
                            onChange={this.onChangePassword}
                            validations={[required, vpassword]}
                          />
                        </div>
                        <div className="form-group relative w-full mb-3">
                          <label 
                            htmlFor="password2"
                            className="block uppercase text-gray-700 text-xs font-bold mb-2"
                          >
                            Re-enter Password
                          </label>
                          <Input
                            type="password"
                            className="form-control px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full"
                            name="password2"
                            placeholder="Password"
                            style={{ transition: "all .15s ease" }}
                            value={this.state.password2}
                            onChange={this.onChangePassword2}
                            validations={[required, vpassword]}
                          />
                        </div>
                        <div className="form-group text-center mt-6">
                        <button
                          className="bg-gray-900 text-white active:bg-gray-700 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 w-full"
                          type="submit"
                          style={{ transition: "all .15s ease" }}
                          title="Coming Soon!"
                          disabled
                        >
                          {this.state.loading && (
                            <span className="spinner-border spinner-border-sm"></span>
                          )}
                          <span>Sign Up</span>
                        </button>
                        </div>
                      </div>
                    )}
                    {this.state.message && (
                      <div className="form-group">
                        <div
                          className={
                            this.state.successful
                              ? "alert alert-success"
                              : "alert alert-danger"
                          }
                          role="alert"
                        >
                          {this.state.message}
                        </div>
                      </div>
                    )}
                    <CheckButton
                      style={{ display: "none" }}
                      ref={c => {
                        this.checkBtn = c;
                      }}
                    />
                  </Form>
                </div>
              </div>
              <div className="flex flex-wrap mt-6">
                <div className="w-1/2">
                  <a
                    href="#pablo"
                    onClick={e => e.preventDefault()}
                    className="text-gray-700"
                  >
                    <small>Forgot password?</small>
                  </a>
                </div>
                <div className="w-1/2 text-right">
                  <Link to="/register">
                    <div
                      className="text-gray-700"
                      style={{ transition: "all .15s ease" }}
                    >
                      <small>Create new account</small>
                    </div>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer absolute />
      </section>
      </main>
      </>
    );
  }
}
