import React from "react";

function LandingFooter(props) {
  return (
    <>
      <footer className={
          (props.absolute
            ? "md:sticky md:w-full md:bottom-auto block py-4"
            : "block py-4")
        }
      >
        <div className="container mx-auto px-4">
          <hr className="mb-4 border-b-1 border-gray-300" />
          <div className="flex flex-wrap items-center md:justify-between justify-center">
            <div className="w-full md:w-4/12 px-4">
              <div className="text-center md:pb-0 pb-5">
              <div className="lg:text-sm text-xs text-gray-600 font-semibold py-1">
                Copyright © {new Date().getFullYear()}{" "}
                <a
                  href="/"
                  className="text-gray-600 hover:text-gray-800 text-sm font-semibold py-1"
                >
                  Authored LLC
                </a>
              </div>
              </div>
            </div>
            <div className="w-full md:w-8/12 px-4">
              <ul className="flex flex-wrap list-none md:justify-end justify-center">
                <li>
                  <a
                    href="/"
                    className="text-gray-700 hover:text-gray-900 lg:text-sm text-xs font-semibold block py-1 px-3"
                  >
                    About Us
                  </a>
                </li>
                <li>
                  <a
                    href="/"
                    className="text-gray-700 hover:text-gray-900 lg:text-sm text-xs font-semibold block py-1 px-3"
                  >
                    FAQ
                  </a>
                </li>
                <li>
                  <a
                    href="/"
                    className="text-gray-700 hover:text-gray-900 lg:text-sm text-xs font-semibold block py-1 px-3"
                  >
                    License
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </footer>
    </>
  );
}

export default LandingFooter;
