import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";

// local imports
import Landing from "./pages/landingIndex";

import AuthService from "./services/auth.service";
import Login from "./components/login.component";
import Register from "./components/register.component";

class App extends Component {

  constructor(props) {
    super(props);
    this.logOut = this.logOut.bind(this);
    this.state = {
      showAuthorBoard: false,
      showAgentBoard: false,
      currentUser: undefined,
    };
  }

  componentDidMount() {
    const user = AuthService.getCurrentUserData();
    if (user) {
      this.setState({
        currentUser: user,
        // TODO get info about user
        // showAuthorBoard: user.roles.includes("ROLE_AUTHOR"),
        // showAgentBoard: user.roles.includes("ROLE_AGENT"),
      });
    }
  }

  logOut() {
    AuthService.logout();
  }

  render() {
    const { currentUser, showAuthorBoard, showAgentBoard } = this.state;
    // Followed this tutorial for React JWT https://www.bezkoder.com/react-jwt-auth/
    return (
      <div>
        <Switch>
          <Route exact path={["/", "/home"]} component={Landing} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/register" component={Register} />
        </Switch>
      </div>
    );
  }

}

export default App;
