import React, { Component } from "react";
import validator from "validator";

class NewsLetterForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            email: '',
            author: false,
            agent: false,
        }
    }

    handleEmailChange = (event) => {
        this.setState({
            email: event.target.value
        })
    }

    handleAuthorCheckboxChange = (event) => {
        this.setState({
            author: !this.state.author,
            agent: false
        })
    }

    handleAgentCheckboxChange = (event) => {
        this.setState({
            agent: !this.state.agent,
            author: false
        })
    }

    handleNewsletterFormSubmit = (event) => {
        var formData = {
            email: this.state.email,
            author: this.state.author,
            agent: this.state.agent
        };

        console.log("Error: no mail client connected");

    }

    render() {
        return (
            <div>
                <div className="flex flex-wrap text-center justify-center">
                <div className="relative w-full mb-2">
                    <label
                    className="md:text-lg uppercase text-gray-700 text-sm font-bold mt-4 mb-4"
                    htmlFor="email"
                    href="mailto:contact@getauthored.com"
                    >
                    contact@getauthored.com
                    </label>
                </div>
                </div>
            </div>
        );
    }
}

export default NewsLetterForm;